import React from 'react';
import './SubmitForm.scss';
import '../Assets/global-styles/bootstrap.css';

const SubmitForm = () => {
    return (
        <section className="SubmitForm">
            <h2>FIND NOTHING?</h2>
            <p>Let us know what you're looking for. We will send you personalized travel plan.</p>
            <form className="Form" method="post" action="#">
                    <div className="form-group">
                        <label className="col-form-label" htmlFor="textarea" >Please descript the ideal trip in your mind below:</label><br></br>
                        <textarea className="form-control" name="message" id="textarea" rows="2" placeholder="My ideal trip should start with ..."></textarea>                                                                      
                    </div>
                    <div className="form-group row Form-row">
                        <label className="col-sm-4 col-form-label" htmlFor="name">Name:</label>
                        <div className="col-sm-8">
                            <input className="form-control" type="text" name="name" id="name" placeholder="Your name" required/>
                        </div>                     
                    </div>
                    <div className="form-group row Form-row">
                        <label className="col-sm-4 col-form-label" htmlFor="contactMethod" >How do you what us to contct you?</label>
                        <div className="col-sm-8">
                            <select className="form-control" name="contactMethod" id="contactMethod">
                                <option value="email" default>email</option>
                                <option value="phone">phone</option>
                                <option value="text">text</option>
                                <option value="mail">mail</option>
                            </select> 
                        </div>
                                                
                    </div>
                    <div className="form-group row">
                        <label className="col-sm-4 col-form-label" htmlFor="email">Email:</label>
                        <div className="col-sm-8">
                            <input className="form-control" type="email" name="email" id="email" placeholder="Your email"/>
                        </div>                         
                    </div>
                    <div className="form-group row Form-row">
                        <div className="col-sm-4" htmlFor="news">Newsletter?</div>
                        <div className="col-sm-8">
                            <div className="form-check">
                                <input className="form-check-input" type="checkbox" name="news" id="news"/>
                                <label className="form-check-label" htmlFor="news">
                                    Yes, please!
                                </label>
                            </div>
                        </div>                        
                    </div>
                    <div className="form-group row Form-row">
                        <label className="col-sm-4"></label>
                        <div className="col-sm-8">
                            <input className="btn btn-outline-success" type="submit" value="Send it!"/>
                        </div>                        
                    </div>
            </form>
            
        </section>
    );
};

export default SubmitForm;