import React from 'react';
import './SearchResult.scss';
import SearchResultWeather from './SearchResultWeather';

const SearchResult =(props) => {

    if (!props.videos) {
        return (
            <div>No Search Result</div>
        );
    };
    
    if (!props.selectedVideo) {
        return (
            <div class="noVideoResult">
                <div class="lds-facebook"><div></div><div></div><div></div></div>
                <div>Please enter a valid place name.</div>
            </div>
        )
    };
        
    const videoSrc = `https://www.youtube.com/embed/${props.selectedVideo.id.videoId}`;

    const searchResult = props.videos.map((video)=>{
        const src=video.snippet.thumbnails.medium.url;
        const alt=video.snippet.title
        return (
            <div key={video.id.videoId} onClick={()=>{props.onSelect(video)}} >
                <img src={src} alt={alt} style={{width:'100%'}} />
                <p style={{fontSize:'70%'}} >{video.snippet.title }</p>
            </div>
        )
    });

    return (
        <div className="row">
            <div className="col-12 col-md-9">
                <div>
                    <SearchResultWeather weather={props.weather} weatherError={props.weatherError} weathertTemperature={props.weathertTemperature} />
                </div>
                <div className="s-video">
                    <iframe title="video player" src={videoSrc} />
                </div>
                <div className="s-videoText">
                    <h4 className="s-videoTitle">{props.selectedVideo.snippet.title}</h4>
                    <p>{props.selectedVideo.snippet.description}</p>
                </div>
            </div>
            <div className="col-12 col-md-3">
                {searchResult}
            </div>
        </div>
    );
};

export default SearchResult;
